.contact-intro {
    max-width: 1200px;
    width: 90%;
    position: relative;
    display: inline-block;
    margin: auto;
    vertical-align: middle;
    padding: 10px;
    color: white;
    text-align: left;
    font-family: "Montserrat","sans-serif"
}

.contact-intro h2 {
    font-weight: 600;
    text-align: left;
    width: 100%;
    padding-bottom: 1rem;
}

.represent-project{
    position: absolute;
    width: 100%;
    text-align: center;
    vertical-align: middle;
}

.represent-content {
    display: flex;
    justify-content: center;
    height: auto;
    width: 100%;
    float: left;
    /* overflow: hidden; */
}

.contact-intro-text{
    width: 30%;
    height: 400px;
    float:left;
    background-color: rgb(0,0,0,0);
    /* margin:auto; */
    border-radius: 15px;
    border: 1px solid #0B5C3B;
    padding:3px;
}
/* .contact-intro-text::after{
    content: '';
    border: 1px solid green;
    width: 100%;
    height: 100%
} */


.represent-text{
    border-radius: 15px;
    height: 100%;
    background-color: #0B5C3B;
    padding:20px;
    font-family: Montserrat,sans-serif
    /* background: white; */
}

.represent-slide-show{
    /* background-color: blue; */
    max-height: 600px;
    padding:30px;
    width: 70%;
    float: left;
}


.contact-separator{
    content: '';
    width: 300px;
    position: relative;
    display: block;
    height: 1px;
    background: -webkit-linear-gradient(90deg,rgba(255,255,255,0) 0,#fff 50%,rgba(255,255,255,0) 100%);
    background: linear-gradient(90deg,rgba(255,255,255,0) 0,#fff 50%,rgba(255,255,255,0) 100%);
    margin: 20px auto;
  }